#banner {
  border-bottom: none;
  margin-top: -20px;
}
#banner h1 {
  font-size: 60px;
  line-height: 1;
  letter-spacing: -1px;
}
.footer {
  text-align: center;
  padding: 30px 0;
  border-top: 1px solid #E5E5E5;
}
.img-responsive {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  display: block;
  background-color: #666;
}
.deckgrid[deckgrid]::before {
  /* Specifies that the grid should have a maximum of 4 columns. Each column will have the classes 'column' and 'column-1-4' */
  content: '1 .column.column-1-1';
  font-size: 0;
  /* See https://github.com/akoenig/angular-deckgrid/issues/14#issuecomment-35728861 */
  visibility: hidden;
}
@media screen and (min-width: 460px) {
  .deckgrid[deckgrid]::before {
    content: '2 .column.column-1-2';
    width: 600px;
  }
}
@media screen and (min-width: 940px) {
  .deckgrid[deckgrid]::before {
    content: '3 .column.column-1-3';
    width: 900px;
  }
}
@media screen and (min-width: 1240px) {
  .deckgrid[deckgrid]::before {
    content: '4 .column.column-1-4';
    width: 900px;
  }
}
.deckgrid.with-search {
  padding-top: 55px;
}
.deckgrid {
  margin-left: 1%;
}
.deckgrid .column {
  float: left;
}
.deckgrid .column-1-4 {
  width: 25%;
}
.deckgrid .column-1-3 {
  width: 33%;
}
.deckgrid .column-1-2 {
  width: 50%;
}
.deckgrid .column-1-1 {
  width: 100%;
}
.deckgrid .image {
  padding: 5px;
  margin: 0px;
}
.deckgrid .selected {
  background-color: #999;
  border-radius: 10px;
}
.deckgrid .image .img-responsive {
  background-color: #666;
}
md-dialog.slide.md-default-theme {
  max-height: 90vh;
  padding: 0px;
  box-shadow: none;
  background: none;
}
md-dialog.slide.md-default-theme md-dialog-content {
  padding: 0px;
  max-height: 100%;
  max-width: 100%;
  overflow: hidden;
  background: none;
}
